import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { motion, AnimatePresence } from 'framer-motion';

interface GenerateUIResponse {
  html: string;
  template_id: string;
  confidence: number;
  verified: boolean;
  status: string;
}

interface FeedbackResponse {
  message: string;
}

interface SaveUIResponse {
  message: string;
  template_id: string;
}

const Home: React.FC = () => {
  const [command, setCommand] = useState('');
  const [htmlOutput, setHtmlOutput] = useState('');
  const [templateId, setTemplateId] = useState('');
  const [confidence, setConfidence] = useState<number | null>(null);
  const [isVerified, setIsVerified] = useState<boolean | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isHelpful, setIsHelpful] = useState<boolean | null>(null);
  const [feedbackComment, setFeedbackComment] = useState('');
  const [userId] = useState(uuidv4()); // Generate unique user ID
  const previewRef = useRef<HTMLDivElement>(null);

  // Handle form submission to generate UI
  const handleGenerate = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);
    setHtmlOutput('');
    setConfidence(null);
    setIsVerified(null);

    try {
      const response = await axios.post<GenerateUIResponse>('http://127.0.0.1:5000/generate-ui', {
        command,
        user_id: userId,
      });

      setHtmlOutput(response.data.html);
      setTemplateId(response.data.template_id);
      setConfidence(response.data.confidence);
      setIsVerified(response.data.verified);
    } catch (err) {
      setError('Failed to generate UI. Please try again.');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  // Handle save UI
  const handleSave = async () => {
    if (!htmlOutput || !templateId) return;

    try {
      const response = await axios.post<SaveUIResponse>('http://127.0.0.1:5000/save-ui', {
        html: htmlOutput,
        parent_template_id: templateId,
        user: userId,
        feedback: isHelpful,
      });
      alert(response.data.message);
    } catch (err) {
      setError('Failed to save template. Please try again.');
      console.error(err);
    }
  };

  // Handle feedback submission
  const handleFeedback = async (helpful: boolean) => {
    setIsHelpful(helpful);
    try {
      const response = await axios.post<FeedbackResponse>('http://127.0.0.1:5000/feedback', {
        template_id: templateId,
        user_id: userId,
        is_helpful: helpful,
        comments: feedbackComment,
      });
      alert(response.data.message);
    } catch (err) {
      setError('Failed to submit feedback. Please try again.');
      console.error(err);
    }
  };

  // Render HTML preview
  useEffect(() => {
    if (previewRef.current && htmlOutput) {
      previewRef.current.innerHTML = htmlOutput;
    }
  }, [htmlOutput]);

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center p-4">
      <h1 className="text-3xl font-bold mb-6 text-gray-800">AI UI Block Generator</h1>

      {/* Input Form */}
      <form onSubmit={handleGenerate} className="w-full max-w-2xl mb-6">
        <div className="flex flex-col space-y-4">
          <textarea
            className="w-full p-4 border rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            rows={4}
            value={command}
            onChange={(e) => setCommand(e.target.value)}
            placeholder="Enter UI command (e.g., 'Create a modern contact form with name, email, and message fields')"
          />
          <button
            type="submit"
            disabled={isLoading}
            className="bg-blue-500 text-white px-6 py-2 rounded-lg hover:bg-blue-600 disabled:bg-blue-300 transition"
          >
            {isLoading ? (
              <span className="flex items-center">
                <svg className="animate-spin h-5 w-5 mr-2" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4z" />
                </svg>
                Generating...
              </span>
            ) : (
              'Generate UI'
            )}
          </button>
        </div>
      </form>

      {/* Error Message */}
      <AnimatePresence>
        {error && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            className="bg-red-100 text-red-700 p-4 rounded-lg mb-6 w-full max-w-2xl"
          >
            {error}
          </motion.div>
        )}
      </AnimatePresence>

      {/* Confidence and Verification Status */}
      {(confidence !== null || isVerified !== null) && (
        <div className="w-full max-w-2xl mb-6">
          <div className="bg-white p-4 rounded-lg shadow-sm">
            <p className="text-sm text-gray-600">
              Confidence Score: {(confidence ?? 0).toFixed(2)} ({isVerified ? 'Verified' : 'Unverified'})
            </p>
            {!isVerified && (
              <p className="text-sm text-yellow-600">This output may need review due to low confidence.</p>
            )}
          </div>
        </div>
      )}

      {/* HTML Preview */}
      {htmlOutput && (
        <div className="w-full max-w-2xl mb-6">
          <h2 className="text-xl font-semibold mb-2">Preview</h2>
          <div
            ref={previewRef}
            className="bg-white p-4 border rounded-lg shadow-sm overflow-auto"
            style={{ minHeight: '200px' }}
          />
          <div className="mt-4 flex space-x-4">
            <button
              onClick={handleSave}
              className="bg-green-500 text-white px-4 py-2 rounded-lg hover:bg-green-600"
            >
              Save Template
            </button>
          </div>
        </div>
      )}

      {/* Feedback Section */}
      {htmlOutput && (
        <div className="w-full max-w-2xl">
          <h2 className="text-xl font-semibold mb-2">Feedback</h2>
          <div className="bg-white p-4 rounded-lg shadow-sm">
            <p className="mb-2">Was this output helpful?</p>
            <div className="flex space-x-4 mb-4">
              <button
                onClick={() => handleFeedback(true)}
                className={`px-4 py-2 rounded-lg ${
                  isHelpful === true ? 'bg-green-500 text-white' : 'bg-gray-200'
                }`}
              >
                üëç Thumbs Up
              </button>
              <button
                onClick={() => handleFeedback(false)}
                className={`px-4 py-2 rounded-lg ${
                  isHelpful === false ? 'bg-red-500 text-white' : 'bg-gray-200'
                }`}
              >
                üëé Thumbs Down
              </button>
            </div>
            <textarea
              className="w-full p-4 border rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              rows={3}
              value={feedbackComment}
              onChange={(e) => setFeedbackComment(e.target.value)}
              placeholder="Optional: Add comments about the output..."
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default Home;
